<template>
  <section class="container">
    <section class="step1 border  mt-5 mx-auto">
      <div class="py-4" v-for="(item,index) in cartData" :key="index">
        <div class="flex-center mx-auto w-75 position-relative">
          <span class="del-btn position-absolute" @click="deleteCartItem(index)">X</span>
          <img :src="item.imgUrl" height="70px">
          <div>
            <a>
              <h5>{{item.fullId}} {{item.title}}-{{item.gender}}</h5>
            </a>
            <h5>規格: {{item.colorObject.name}} - {{item.sizeObject.name}}</h5>
          </div>
          <div class="border rounded d-flex ml-auto">
            <a class="border-right px-4 flex-center" @click="item.qty? item.qty-= 1: item.qty+0">-</a>
            <input
              type="text"
              class="px-2 border-0 text-center qty-input"
              v-model.number="item.qty"
            >
            <a class="border-left px-4 flex-center" @click="item.qty += 1">+</a>
          </div>
          <div class="flex-center ml-4">$ {{item.qty*item.price}}</div>
        </div>
      </div>
    </section>
    <section class="step2 border  mt-5 mx-auto ">
      <h4 class="border-bottom py-4 w-75 mx-auto">台灣及離島</h4>
      <div class="py-4 w-75 mx-auto">
        <div class="flex-center">
          <input type="radio" value="7-11取貨付款" class="mr-2" id="711" v-model="payType">
          <label class="mb-0" for="711">7-11取貨付款</label>
          <div class="ml-auto">滿 1000 元免運，運費 $ 0	</div>
        </div>
        <div class="text-price" v-if="alertShow.payType">此欄為必填</div>
      </div>
    </section>
    <section class="price-section  d-flex mb-5">
      <table class="ml-auto w-50 text-right p-2">
        <td>
          <tr>共{{totalNum}}件</tr>
          <tr></tr>
          <tr></tr>
          <tr></tr>
        </td>
        <td>
          <tr>商品金額</tr>
          <tr>運費小計</tr>
          <tr>帳戶扣抵</tr>
          <tr>總計</tr>
        </td>
        <td>
          <tr>${{totalPrice}}</tr>
          <tr>$0</tr>
          <tr>$0</tr>
          <tr>${{totalPrice}}</tr>
        </td>
      </table>
    </section>
    <section class="d-flex">
      <router-link :to="{name: 'index'}" class="bg-primary rounded text-center py-3 text-white w-20">
        繼續購物
      </router-link>
      <a @click="routeToCheck2" class="bg-primary ml-auto rounded text-center py-3 text-white w-20">
        下一步
      </a>
    </section>
  </section>
</template>
<script>
import Card from "@/components/singleCard.vue";
export default {
  name: "check1",
  props:['step'],
  data() {
    return {
      cartData: {},
      payType: '',
      alertShow:{
        payType: false
      }
    };
  },
  components: {
    Card
  },
  methods: {
    deleteCartItem(index) {
      this.cartData.splice(index, 1)
    },
    changeStoreCart(){
    },
    routeToCheck2() {
      if(this.payType){
        this.$store.commit("CHECKCART",this.cartData)
        this.$store.commit("TOTALPRICE",this.totalPrice)
        this.$store.commit("SETPAYTYPE",this.payType)
        this.$router.push('/check/2')
      }else{
        this.alertShow.payType = true
      }
    }
  },
  computed: {
    totalNum() {
      let tempNum = 0
      this.cartData.forEach(element => {
        tempNum += element.qty
      });
      return tempNum
    },
    totalPrice() {
      let tempPrice = 0
      this.cartData.forEach(element => {
        tempPrice += element.qty*element.price
      })
      return tempPrice
    },
  },
  created() {
    this.cartData = this.$store.state.cart;
    this.$store.commit('CHECKSTEP', 1)
  }
};
</script>
<style lang="scss" scoped>

.qty-input {
  width: 50px;
}
.del-btn {
  top: 0px;
  right: 0px;
  color: #8f8f8f;
  font-size: 16px;
  &:hover {
    cursor: pointer;
    color: #573d33;
  }
}
.price-section{
  background-color: #fbfbfb;
}
table,td,tr{
  padding:12px;
}
.step2{
  color: #706e6c;
  input{
    height: 16px;
    width: 16px;
  }
}
h1,h2,h3,h4,h5,h6,.h1,h2,h3,h4,h5,h6{
  margin-bottom: 0;
}
</style>