<template>
  <div class="card-btn">
    <router-link class="d-block" tag="div" :to="{ name: 'productDetail', params: { 'productId': productId }}">
      <img class="w-100 mb-3" :src="imgUrl" alt="">
      <div class="computer-view d-none d-sm-block">
        <div class="size-group d-flex mb-3 justify-content-center">
          <div v-for="(item,index) in simpleData.sizes" :key="index" class="size-btn d-flex bg-sizebg align-items-center justify-content-center mr-1">
            {{item.name}}
          </div>
        </div>
        <h6 class="text-center mb-3">
          {{simpleData.title}}-{{simpleData.gender}}
        </h6>
        <h6 class="text-center">
          NT${{simpleData.price}}
        </h6>
      </div>
      <div class="mobile-view d-block d-sm-none">
        <h6 class="mb-1">{{simpleData.title}}</h6>
        <h6>${{simpleData.price}}</h6>
      </div>
    </router-link>
  </div>
</template>
<script>
export default {
  name: 'card',
  props: ["productId"],
  computed: {
    imgUrl() {
      const url = require(`@/assets/productsData/${this.productId}/1.jpg`)
      return url
    }
  },
  data() {
    return {
      simpleData: {}
    }
  },
  created() {
    this.simpleData = this.$store.state.allProducts[this.productId]
  }
}

</script>
<style lang="scss" scoped>
.size-btn {
  width: 22px;
  height: 22px;
}

.card-btn {
  cursor: pointer;
}

</style>
